

























































.progress {
    overflow: hidden;

    &-wrapper {
        position: relative;
        height: 16px;
        float: left;
    }

    &-empty {
        width: 100%;
        height: inherit;
        border-radius: 8px;
        background: #ECEDF6;
        position: absolute;
        left: 0;
        right: 0;
    }

    &-value {
        height: inherit;
        border-radius: 8px;
        position: absolute;
        left: 0;
        right: 0;
        transition: all .3s;
    }

    &-text {
        height: inherit;
        line-height: 16px;
        float: right;
        width: 35%;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        margin: 0;
        font-size: 14px;
    }
}
